import React from 'react';
import Layout from '../components/layout';
import { graphql } from 'gatsby';

import {Grid, Row, Col} from 'react-bootstrap';



const styles = {
	intro: {
		lineHeight: '1.8em',
		meta: {
			color: 'grey',
			listElement: {
				fontSize: '0.95em',
				padding: '0px 10px',
			}
		}
	},

	body: {
		paddingTop: '50px',	
		backgroundColor: 'white',
		color: 'black',
	}
}

const KoalaJoeyLanding = (props) => {
	const post = props.data.markdownRemark;
	const {title, date, team, tools, methods, duration, image, video, description, showcase} = post.frontmatter;
	const {html} = post;

	console.log(post.frontmatter);
	return (

		<Layout>
			<Grid className="pb-5" style={styles.intro}>
				<Row>
					<Col md={8} mdOffset={2} className="of-contain">
						<img src={`../${image}`} alt="Glamour shot of product"/>
					</Col>
					<Col md={8} mdOffset={2} className="mt-2">
						<h2>{title}</h2>
						<p className="deemphasize">{date} - ({duration})</p>
						<p>{description}</p>
					</Col>
				</Row>

				<Row>
				<Col md={4} mdOffset={2}>
						<ul className="list-inline mt-2" style={styles.intro.meta}>
								<li className="emphasize">Team: </li>
								{team.map( (el, i) => (
									<li key={`team-${i}`}>{el}</li>
								))}
						</ul>
					</Col>
				</Row>
				<Row style={styles.intro.meta}>
					<Col md={4} mdOffset={2}>
						<h6 style={styles.intro.meta}>Methods:</h6>
						<ul className="list-inline">
							{methods.map( (el, i) => (
								<li style={styles.intro.meta.listElement} key={`methods-${i}`}>{el}</li>
							))}
						</ul>
					</Col>
					<Col md={4}>
					<h6 style={styles.intro.meta}>Tools:</h6>
						<ul className="list-inline" style={styles.intro.meta}>
							{tools.map( (el, i) => (
								<li style={styles.intro.meta.listElement} key={`tools-${i}`}>{el}</li>
							))}
						</ul>
					</Col>
				</Row>
			</Grid>

			<Grid fluid style={styles.body} clas>
				<Grid>
					<Row>
						<Col md={8} mdOffset={2}>
							<div className="portfolio-body mt-1 mb-4" dangerouslySetInnerHTML={{__html: html }}></div>
						</Col>
					</Row>
				</Grid>
			</Grid>

			<Grid fluid>
				<Grid id="concept-generation">
					<Row className="mt-2 pb-5">
						<Col md={5} mdOffset={2} sm={4} smOffset={2} >
							<video src={`../${video}`} autoPlay muted loop width="100%" ></video>
						</Col>
						<Col md={3} sm={6} smOffset={1} className="flex-row-center">
							<div>
								<h2 className="mt-0">Showcase</h2>
								<p className="p-large">{showcase}</p>
							</div>
						</Col>
					</Row>
				</Grid>
			</Grid>

		</Layout>
	)
}

export default KoalaJoeyLanding;

export const query = graphql`
 	query KoalaJoeyLanding($slug: String!) {
		markdownRemark(fields: { slug: { eq: $slug }  }) {  
			html
			frontmatter {
				title
				date(formatString: "Do MMMM YYYY")
				team
				tools
				image
				video
				showcase
				description
				methods
				duration
			}	
		}
	}
`
